import React, { useState, useEffect } from 'react'
import addIcon from '../../images/add.png'

import './style.css'

export interface MenuItem {
  text?: string;
  value?: string;
}

interface SideMenuProps {
  list: MenuItem[];
  defaultValue?: string;
  onChange?: (value: string) => void;
  onEditor?: () => void;
}

const SideMenu: React.FC<SideMenuProps> = (props) => {
  const { list, defaultValue, onChange, onEditor } = props;
  const [selectedValue, setSelectedValue] = useState(defaultValue)

  const handleClick = (value?: string) => {
    if (value !== selectedValue) {
      value && onChange?.(value)
    }
    setSelectedValue(value)
  }

  useEffect(() => {

  }, [])

  return (
    <ul className="side_menu">
      {list.map(item => {
        const selected = selectedValue === item.value
        return (
          <li onClick={() => handleClick(item.value)} className={`${selected ? 'side_menu_item__selected' : null} side_menu_item`} key={item.value}>
            {selected && <span onClick={onEditor}>editor</span>}
            {item.text}
          </li>
        )
      })}
    </ul>
  )
}

export default SideMenu
